import React from 'react'
import './InfoBox.css'

export default function InfoBox({info}) {
    let tags, description;
    if (info.tags) {
        tags = (info.tags.length) ? info.tags : undefined
    }
    if (info.description) description = info.description
    return (
        <React.Fragment>
            {
                (description)
                    ? <div className="description"><h3>Location:</h3> <span>{description}</span></div>
                    : undefined
            }
            {
                (tags)
                    ? <div className="tags"><h3>Location Tags:</h3> <span>{tags.map((text, i) => <div key={`loctag-${i}`} className='tag'>{text}</div>)}</span></div>
                    : undefined
            }
        </React.Fragment>
    )
}