import React, { useState } from 'react'
import Autocomplete from '../../Autocomplete'
import GoogleMap from '../../GoogleMap'
import InfoBox from './InfoBox'
import { useResize } from '../hooks'
import '../Slides.css'

export default function () {
    /*
     * LOCATION QUERY
     */
    const [locationName, setLocationName] = useState("Blooming Pixel Creatives")
    const [locationInfo, setLocationInfo] = useState({})
    const handleSelectionChange = (newSelection) => {
        setLocationName(newSelection.description)
        setLocationInfo(newSelection)
    }


    /*
     *  UE EFFECTS
     */
    const [responsiveClassName, setResponsiveClassName] = useState('')
    const responsiveClassNameEffect = useResize(([windowHeight, windowWidth]) => {
        if (windowHeight > windowWidth*0.825) {
            setResponsiveClassName('mobile')
        } else {
            setResponsiveClassName('')
        }
    })

    return (
        <React.Fragment>
            <div className={`mapViewFlexbox ${responsiveClassName}`}>
                <div className="search">
                    <div className="autocompleteContainer">
                        <Autocomplete
                            themeClass={`light loc-browser`}
                            onSelectionChange={handleSelectionChange}
                        />
                    </div>

                    <div className='infoBox'>
                        <InfoBox info={locationInfo} />
                    </div>
                </div>

                <div className="map">

                    <div style={{flex: 0}} />
                    <div style={{flex: 8, position: 'relative', height: '100%'}}>
                        <GoogleMap locationName={locationName} />
                    </div>
                    <div style={{flex: 0}} />
                </div>
            </div>
        </React.Fragment>
    )
}
